@use "global" as *;


.p-article {
  @include mq("md") {
    display: flex;
    justify-content: center;
    align-items: start;
  }
}

.p-article__header {
  display: flex;
  align-items: center;
}

.p-article__label {
  display: inline-block;
  width: rem(100);
  max-width: 100%;
  font-size: rem(14);
  font-weight: $medium;
  padding: rem(6) rem(6) rem(3);
  border: 1px solid $color-white;
  text-align: center;
  margin-left: rem(32);

  @include mq("md") {
    margin-left: rem(48);
  }
}

.p-article__content {
  display: inline-block;
  text-align: left;
  margin-top: rem(16);
  
  @include mq("md") {
    text-align: left;
    margin-top: rem(5);
    flex: 1 1;
    margin-left: rem(60);
  }
}

.p-article__footer {
  text-align: center;
  margin-top: rem(28);

  @include mq("md") {
    margin-top: 0;
    margin-left: rem(40);
  }
}

/*---top---*/

.p-article__content:hover,
.p-article__content:focus {
  color: $color-yellow-dark;
}

.p-article--top .p-article__content {
  position: relative;
}

.p-article--top .p-article__content:hover,
.p-article--top .p-article__content:focus {
  color: $color-white;
}

.p-article--top .p-article__content::after {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: rem(1);
  background: transparent;
  bottom: 0;
  left: 0;
  transition: .3s ease;
}

.p-article--top .p-article__content:hover::after, 
.p-article--top .p-article__content:focus::after {
  background: $color-white;
} 
