@use "global" as *;

.p-media {
  @include mq('md') {
    display: flex;
    align-items: center;
  }
}

.p-media__img {
  width: 100vw;
  aspect-ratio: 557 / 389;
  margin: 0 calc(50% - 50vw);
  position: relative;

  @include mq("md") {
    width: calc(557 / 1100 * 100%);
    margin: 0;
  }
}

.p-media__img > img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.p-media__content {
  margin-top: rem(25);
  text-align: center;
  letter-spacing: rem(2);

  @include mq("md") {
    margin-top: 0;
    width: calc(510 / 1100 * 100%);
    margin-left: rem(32);
    text-align: left;
  }
}

.p-media__title {
  font-size: rem(20);
  letter-spacing: rem(2);

  @include mq("md") {
    font-size: rem(25);
    letter-spacing: rem(0.5);
  }
}

.p-media__text {
  text-align: left;
  line-height: rem(23.17);
  margin-top: rem(22);

  @include mq("md") {
    line-height: rem(35);
    letter-spacing: rem(0.5);
    margin-top: rem(36);
  }
}

.p-media__footer {
  margin-top: rem(27);

  @include mq("md") {
    margin-top: rem(48);
  }
}

// reverse

.p-media--pc-rev {
  flex-direction: row-reverse;
}

.p-media--pc-rev .p-media__content {
  @include mq('md') {
    margin-left: 0;
  }
}

.p-media--pc-rev .p-media__img {
  @include mq('md') {
    margin-left: rem(32);
  }
}

// ai-start

.p-media--ai-start {
  @include mq('md') {
    align-items: start;
  }
}