@use "global" as *;

.p-page-content .p-page-mv {
  background: transparent url(../img/content/mv_content_sp.jpg) no-repeat center center / cover;

  @include mq('md') {
    background: transparent url(../img/content/mv_content_pc.jpg) no-repeat center center / cover;
  }
}

.p-page-content__body {
  margin-top: rem(85);

  @include mq('md') {
    margin-top: rem(104);
  }
}

.p-page-content__medias {
  margin-top: rem(80);

  @include mq('md') {
    margin-top: rem(150);
  }
}

.p-page-content__title {
  font-size: rem(20);
  letter-spacing: rem(2);
  text-align: center;

  @include mq('md') {
    font-size: rem(25);
  }
}

.p-page-content__lead {
  margin-top: rem(12);
  letter-spacing: rem(2);
  line-height: calc(35 / 16 * 100%);

  @include mq('md') {
    text-align: center;
  }
}