@use "global" as *;

.p-cards-col-4 {
  width: 100%;

  @include mq('md') {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}

.p-cards-col-4__item {
  width: 100%;

  @include mq('md') {
    width: calc((100% - rem(32) * 3) / 4);
  }
}

.p-cards-col-4__item + .p-cards-col-4__item {
  margin-top: rem(24);

  @include mq('md') {
    margin-top: 0;
    margin-left: rem(32);
  }
}