@use "global" as *;

.p-main-visual {
  height: 100vh;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.p-main-visual__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
  letter-spacing: rem(0.5);
  z-index: map-get($layer, default) + 1;
}

.p-main-visual__title {
  font-size: rem(28);
  font-weight: $bold;

  @include mq("md") {
    font-size: rem(50);
    margin-top: rem(-5);
  }
}

.p-main-visual__subtitle {
  font-size: rem(20);
  font-weight: $bold;
  margin-top: rem(12);

  @include mq("md") {
    font-size: rem(25);
    margin-top: rem(4);
  }
}
