@use "global" as *;

@use "global" as *;
  
.p-single-blog__breadcrumbs {
  margin-top: rem(66);
}

.p-single-blog__body {
  margin-top: rem(50);

  @include mq('md') {
    margin-top: rem(90);
  }
}

.p-single-blog__inner.l-inner {
  max-width: calc(rem(800) + $padding-pc * 2);
}

.p-single-blog__title {
  font-size: rem(20);
  letter-spacing: rem(.4);

  @include mq('md') {
    font-size: rem(23);
    font-weight: 500;
  }
}

.p-single-blog__info {
  display: flex;
  align-items: center;
  margin-top: rem(16);

  @include mq('md') {
    margin-top: rem(32);
  }
}

.p-single-blog__date {
  display: inline-block;
  letter-spacing: rem(.4);
}

.p-single-blog__genre {
  display: inline-block;
  letter-spacing: rem(2);
  padding: rem(6) rem(20);
  border: rem(1) solid $color-white;
  margin-left: rem(30);
}

.p-single-blog__thumb {
  width: 100vw;
  position: relative;
  margin: rem(16) calc(50% - 50vw) 0 ;

  @include mq('md') {
    width: 100%;
    margin: rem(32) 0 0;
  }
}

.p-single-blog__thumb::after {
  content: "";
  display: block;
  width: 100%;
  padding-top: calc(251 / 375 * 100%); 

  @include mq('md') {
    padding-top: calc(427 / 799 * 100%);
  }
}

.p-single-blog__thumb img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
}

.p-single-blog__content {
  margin-top: rem(24);

  @include mq('md') {
    margin-top: rem(32);
  }
}

.p-single-blog__pagination {
  margin-top: rem(80);

  @include mq('md') {
    margin-top: rem(104);
  }
}

.p-single-blog__related {
  margin-top: rem(120); 

  @include mq('md') {
    margin-top: rem(104);
  }
}
