@use "global" as *;

.p-square-img {
  display: block;
  overflow: hidden;
  position: relative;
}

.p-square-img::before {
  content: "";
  display: block;
  width: 100%;
  padding-top: calc(168 / 188 * 100%);

  @include mq('md') {
    padding-top: 100%;
  }
}

.p-square-img::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: $color-white;
  transition: all 0.3s;
  opacity: 0;
}

.p-square-img:hover::after,
.p-square-img:focus::after {
  opacity: 0.6;
}


.p-square-img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
}

.p-square-img__title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: rem(20);
  letter-spacing: rem(2);
  width: 100%;
  text-align: center;
  z-index: map-get($layer, default) + 1;

  @include mq("md") {
    font-size: rem(25);
    letter-spacing: rem(0.5);
  }
}