@use "global" as *;

.p-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: rem(50);
  line-height: rem(50);
  z-index: map-get($layer, header);
  background: rgba(17, 17, 17, 0.5);
  transition: all 0.3s;
}

.p-header--opacity-offset {
  background: $color-black;
}

.p-header__inner {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.p-header__logo {
  padding-left: rem(22);
  display: inline-block;
  display: flex;
  justify-content: center;
  align-items: center;

  @include mq("md") {
    padding-left: rem(32);
  }
}

.p-header__logo__link {
  font-family: $logo-font-family;
  font-size: rem(20);
  letter-spacing: rem(1.2);

  @include mq("md") {
    font-size: rem(30);
    line-height: rem(50);
  }
}

.p-header__icon {
  padding-right: rem(22);
}

.p-header__list {
  @include mq("md") {
    display: flex;
    justify-content: flex-end;
  }
}

.p-header__item + .p-header__item {
  margin-left: rem(30);
  letter-spacing: rem(2);
}

.p-header__link {
  letter-spacing: rem(2);
  line-height: rem(50);
  display: inline-block;
  position: relative;
}

.p-header__link::after {
  content: "";
  position: absolute;
  bottom: rem(6);
  left: 0;
  width: 100%;
  height: rem(1);
  background: $color-yellow-light;
  transition: opacity 0.3s;
  opacity: 0;
}

.p-header__link:hover::after,
.p-header__link:focus::after {
  opacity: 1;
}

.p-header__link--contact {
  padding: 0 rem(31);
  background: $color-white;
  color: $color-main;
  transition: 0.3s;
}

.p-header__link--contact::after {
  display: none;
}

.p-header__link--contact:hover,
.p-header__link--contact:focus {
  background: $color-main;
  color: $color-white;
  text-decoration: none;
  opacity: 1;
}
