@use "global" as *;

.p-cards {
  @include mq("md") {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    justify-content: flex-start;
  }
}

.p-cards__item {
  width: 100%;

  @include mq("md") {
    width: calc(1 / 3 * 100% - rem(50) / 3);
  }
}

.p-cards__item + .p-cards__item {
  margin-top: rem(24);
  
  @include mq('md') {
    margin-top: 0;
  }
}

.p-cards__item:nth-child(3n + 2),
.p-cards__item:nth-child(3n) {
  @include mq('md') {
    margin-top: 0;
    margin-left: rem(24);
  }
}

.p-cards__item:nth-child(n + 4) {
  @include mq('md') {
    margin-top: rem(48);
  }
}