@use "global" as *;

.c-hamburger-icon {
  display: block;
  height: rem(2);
  width: rem(24);
  background: $color-white;
  position: relative;
  transition: transform 0.3s ease;
}

.c-hamburger-icon.is-open {
  height: 0;
}

.c-hamburger-icon.is-open::before {
  transform: translate(rem(-12), rem(5.5)) rotate(45deg);
}

.c-hamburger-icon.is-open::after {
  transform: translate(rem(-12), rem(-5.5)) rotate(-45deg);
}

.c-hamburger-icon::before {
  content: "";
  position: absolute;
  top: rem(-6);
  left: 50%;
  transform: translateX(-50%);
  display: block;
  height: rem(2);
  width: rem(24);
  background: $color-white;
  transition: transform 0.3s ease;
}

.c-hamburger-icon::after {
  content: "";
  position: absolute;
  bottom: rem(-6);
  left: 50%;
  transform: translateX(-50%);
  display: block;
  height: rem(2);
  width: rem(24);
  background: $color-white;
  transition: transform 0.3s ease;
}
