@use "global" as *;

.p-underline-list__row {
  display: flex;
  padding: rem(14) 0 rem(18);
  border-bottom: rem(1) solid $color-white;
  align-items: center;

  @include mq('md') {
    padding: rem(28) 0;
  }
}

.p-underline-list__term {
  font-size: rem(13);
  width: rem(80);

  @include mq('md') {
    font-size: rem(16);
    width: rem(110);
  }
}

.p-underline-list__desc {
  font-size: rem(13);
  line-height: calc(24 / 13 * 100%);
  width: 100%;
  margin-left: rem(29);

  @include mq('md') {
    font-size: rem(16);
    margin-left: rem(42);
    line-height: calc(24 / 16 * 100%);
  }
}
