@use "global" as *;

.p-404 {
  padding: rem(130) 0 rem(120);

  @include mq('md') {
    padding: rem(200) 0 rem(150);
  }
}

.p-404__title {
  font-size: rem(40);
  letter-spacing: rem(2);
  text-align: center;
  color: $color-yellow-dark;
  opacity: .3;

  @include mq('md') {
    font-size: rem(60);
  }
}

.p-404__text {
  font-size: rem(20);
  letter-spacing: rem(2);
  text-align: center;
  margin-top: rem(80);

  @include mq('md') {
    font-size: rem(30);
  }
}

.p-404__footer {
  text-align: center;
  margin-top: rem(80);
}