@use "global" as *;

.p-page-overview .p-page-mv {
  background: transparent url(../img/overview/mv_overview_sp.jpg) no-repeat center center / cover;

  @include mq('md') {
    background: transparent url(../img/overview/mv_overview_pc.jpg) no-repeat center center / cover;
  }
}

.p-page-overview__body {
  margin-top: rem(85);

  @include mq('md') {
    margin-top: rem(115);
  }
}

.p-page-overview__map {
  width: 100vw;
  margin: rem(120) calc(50% - 50vw) 0;
  position: relative;

  @include mq('md') {
    width: 100%;
    margin: rem(80) 0 0;
  }
}

.p-page-overview__map::after {
  content: "";
  display: block;
  width: 100%;
  padding-top: calc(300 / 375 * 100%);

  @include mq('md') {
    padding-top: calc(367 / 1100 * 100%);    
  }
}

.p-page-overview__map iframe {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

