@use "global" as *;

/*wp-pagenavi 01*/
.wp-pagenavi {
  clear: both;
  text-align: center;

}
.wp-pagenavi a,
.wp-pagenavi span {
  border: rem(1) solid $color-white;
  padding: rem(3) rem(8);
  margin: 0 rem(8);
  white-space: nowrap;
  transition: 0.2s ease-in-out;
  text-align: center;
  text-decoration: none;
}
.wp-pagenavi a:hover,
.wp-pagenavi a:focus,
.wp-pagenavi span.current {
  color: $color-black;
  background-color: $color-white;
}

.wp-pagenavi .previouspostslink,
.wp-pagenavi .nextpostslink {
  margin: 0 rem(16);
  letter-spacing: rem(2);
  padding: rem(2) rem(13);
}
