@use "global" as *;

.p-post-content > * {
  margin-top: rem(24);

  @include mq('md') {
    margin-top: rem(32);
  }
}

.p-post-content > p {
  font-size: rem(16);
  letter-spacing: rem(.4);
  line-height: calc(35 / 16);
}

.p-post-content > h2 {
  font-size: rem(20);
  letter-spacing: rem(2);
  padding-left: rem(9);
  position: relative;
}

.p-post-content > h2::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: rem(3);
  height: 100%;
  background: #C4C4C4;
}

.p-post-content > h3 {
  font-size: rem(18);
  letter-spacing: rem(2);
  margin-left: rem(9);
  position: relative;
  display: inline-block;
}

.p-post-content > h3::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: rem(3);
  background: $color-white;
}

.p-post-content > h3::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: calc(73 / 214 * 100%);
  height: rem(3);
  background: $color-yellow-light;
} 

.p-post-content > figure {
  width: 100vw;
  margin-right: calc(50% - 50vw);
  margin-left: calc(50% - 50vw);

  @include mq('md') {
    width: auto;
    margin-right: 0;
    margin-left: 0;
  }
}

.p-post-content > ul li {
  font-size: rem(16);
  letter-spacing: rem(.4);
  line-height: calc(35 / 16);
}

.p-post-content > ul li::before {
  content: "・";
  display: inline-block;
}

.p-post-content > ol {
  counter-reset: number 0;
}

.p-post-content > ol li {
  font-size: rem(16);
  letter-spacing: rem(.4);
  line-height: calc(35 / 16);
}

.p-post-content > ol li::before {
  counter-increment: number 1;
  content: counter(number);
  display: inline-block;
  padding-right: .5em;
}