@use "global" as *;

.c-button-submit {
  display: inline-block;
  font-size: rem(20);
  letter-spacing: rem(2);
  padding: rem(10);
  background: $color-white;
  color: $color-black;
  width: rem(360);
  max-width: 100%;
  text-align: center;

  @include mq('md') {
    font-size: rem(16);
    padding: rem(18);
  }
}

.c-button-submit:hover,
.c-button-submit:focus {
  background: $color-main;
  color: $color-white;
}