@use "global" as *;

.p-card {
  display: block;
  position: relative;
  z-index: map-get($layer, default) + 1;
}

.p-card:hover .p-card__body,
.p-card:focus .p-card__body {
  background: $color-main;
  color: $color-white;
}

.p-card__figure {
  display: block;
  width: 100%;
  position: relative;
}

.p-card__figure::after {
  content: "";
  display: block;
  width: 100%;
  padding-top: calc(233 / 350 * 100%);
}

.p-card__figure img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
}

.p-card__body {
  background: $color-white;
  color: $color-main;
  letter-spacing: rem(1);
  line-height: rem(26);
  font-size: rem(14);
  padding: rem(4) rem(23) rem(16);
  transition: all 0.3s;
}

.p-card__title {
  font-size: rem(18);
  font-weight: $medium;
  line-height: rem(35);
}

.p-card__lead {
  margin-top: rem(3);

  @include mq("md") {
    margin-top: rem(6);
  }
}

.p-card__data {
  display: flex;
  justify-content: space-between;
  margin-top: rem(6);

  @include mq("md") {
    margin-top: rem(10);
  }
}

.p-card__label {
  background: $color-yellow-light;
  padding: 0 rem(4);
  color: $color-black;
}

.p-card__new {
  position: absolute;
  left: rem(-15);
  top: rem(-15);
  line-height: rem(35);
  letter-spacing: rem(2);
  background: #ffba6a;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: rem(60);
  height: rem(60);
  font-size: rem(16);
  color: $color-main;
  z-index: map-get($layer, default) + 1;
}

.p-card__new.u-mobile {
  @include mq('md') {
    display: none;
  }
}

.p-card--col4 .p-f {
  @include mq('md') {
    padding: rem(3) rem(9) rem(14);
  }
}

.p-card--col4 .p-card__title {
  font-size: rem(14);
  font-weight: 500;
}