@use "global" as *;

.p-page-news .p-page-mv {
  background: transparent url(../img/news/mv_news_sp.jpg) no-repeat center center / cover;

  @include mq('md') {
    background: transparent url(../img/news/mv_news_pc.jpg) no-repeat center center / cover;
  }
}

.p-page-news__body {
  margin-top: rem(85);

  @include mq('md') {
    margin-top: rem(69);
  }
}

.p-page-news__inner.l-inner {
  max-width: calc(rem(860) + $padding-pc * 2);
}

.p-page-news__pagination {
  margin-top: rem(80);

  @include mq('md') {
    margin-top: rem(104);
  }
}