@use "global" as *;

.c-button {
  display: inline-block;
  width: rem(360);
  max-width: 100%;
  padding: rem(8) rem(14);
  font-size: rem(20);
  text-align: center;
  background: $color-main;
  border: 1px solid $color-white;
  letter-spacing: rem(2);
  background: linear-gradient(to right, $color-white 50%, $color-main 50%);
  background-size: 200% 100.5%;
  background-position: right bottom;
  transition: all 0.3s ease-out;

  @include mq("md") {
    font-size: rem(16);
    font-weight: $medium;
    line-height: rem(54);
    padding: rem(2) rem(14);
  }
}
.c-button:hover,
.c-button:focus {
  background-position: left bottom;
  color: $color-main;
  opacity: 1;
}

.c-button--pc-fs20 {
  @include mq("md") {
    font-size: rem(20);
    font-weight: $regular;
    text-align: center;
  }
}

.c-button--pc-fill {
  @include mq("md") {
    color: $color-black;
    width: rem(134);
    padding: rem(3) rem(14);
    font-size: rem(16);
    border: none;
    letter-spacing: 0;
    line-height: inherit;
    background: linear-gradient(to right, $color-main 50%, $color-white 50%);
    background-size: 200% 100.5%;
    background-position: right bottom;
  }

  .c-button--pc-fill:hover,
  .c-button--pc-fill:focus {
    @include mq('md') {
      background-position: left bottom;
      color: $color-white;
      opacity: 1;
    }
  }
}