@use "global" as *;

.p-cf7 {
  position: relative;
}

.p-cf7__row {
  @include mq("md") {
    position: relative;
  }
}

.p-cf7__row + .p-cf7__row {
  margin-top: rem(32);

  @include mq("md") {
    margin-top: rem(45);
  }
}

.p-cf7__label {
  font-size: rem(14);
  letter-spacing: rem(0.7);

  @include mq("md") {
    position: absolute;
    top: rem(22);
    left: 0;
    transform: translate(calc(-100% - rem(24)), -50%);
    text-align: right;
  }
}

.p-cf7__input input[type="text"],
.p-cf7__input textarea {
  background: $color-white;
  padding: rem(9);
  margin-top: rem(16);
  width: 100%;

  @include mq("md") {
    margin-top: 0;
    padding: rem(7) rem(16);
  }
}

.p-cf7__input input[type="text"]::placeholder,
.p-cf7__input textarea::placeholder {
  font-size: rem(14);
  letter-spacing: rem(0.7);
}

.p-cf7__input input.wpcf7-not-valid,
.p-cf7__input textarea.wpcf7-not-valid {
  background: #ffdcdc !important;
}

.p-cf7__submit {
  margin-top: rem(48);
  text-align: center;

  @include mq("md") {
    margin-top: rem(56);
  }
}

.p-cf7__submit span {
  display: none;
}

.p-cf7 .wpcf7-not-valid-tip {
  display: none;
  font-size: rem(16);
  font-weight: 400;
  letter-spacing: rem(2);
  color: #FF5757;
  position: absolute;
  bottom: calc(100% + rem(30));
  left: 50%;
  transform: translateX(-50%);
}

.p-cf7 .p-cf7__error-wrap .wpcf7-not-valid-tip {
  display: block;
}

.p-cf7 .wpcf7-response-output {
  display: none;
}