@use "global" as *;

.p-page-thanks {
  padding: rem(130) 0 rem(120);

  @include mq('md') {
    padding: rem(200) 0 rem(150);
  }
}

.p-page-thanks__title {
  font-size: rem(20);
  letter-spacing: rem(2);
  text-align: center;

  @include mq('md') {
    font-size: rem(30);
  }
}

.p-page-thanks__text {
  font-size: rem(20);
  letter-spacing: rem(2);
  text-align: center;
  margin-top: rem(80);

  @include mq('md') {
    font-size: rem(30);
  }
}

.p-page-thanks__footer {
  text-align: center;
  margin-top: rem(80);
}