@use "global" as *;

.p-page-contact .p-page-mv {
  background: transparent url(../img/contact/mv_contact_sp.jpg) no-repeat center center / cover;

  @include mq('md') {
    background: transparent url(../img/contact/mv_contact_pc.jpg) no-repeat center center / cover;
  }
}

.p-page-contact__inner.l-inner {
  max-width: calc(rem(540) + $padding-pc * 2);
}

.p-page-contact__body {
  margin-top: rem(85);
  padding-bottom: rem(120);

  @include mq('md') {
    margin-top: rem(115);
    padding-bottom: rem(150);
  }
}