@use "global" as *;

.p-related__header {
  font-size: rem(16);
  letter-spacing: rem(2);
  text-align: center;
  padding: rem(8) 0;
  border: rem(1) solid $color-white;

  @include mq('md') {
    background: $color-main;
  }
}

.p-related__body {
  margin-top: rem(32);

  @include mq('md') {
    margin-top: rem(80);
  }
}