@use "global" as *;

.p-desc-box {
  display: block;
  padding: 0 rem(10) rem(30);
  position: relative;

  @include mq('md') {
    padding: 0 rem(22) rem(30);
  }
}

.p-desc-box::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: calc(100% - rem(21));
  border: rem(1) solid $color-white;
  z-index: map-get($layer, bg);
}

.p-desc-box__title {
  display: inline-block;
  font-size: rem(20);
  letter-spacing: rem(.4);
  padding: rem(6) rem(24);
  background: $color-black;
  border: rem(1) solid $color-white;
  border-radius: rem(42);
  margin-left: rem(24);

  @include mq('md') {
    margin-left: rem(39);
  }
}

.p-desc-box__text {
  letter-spacing: rem(.4);
  line-height: calc(35 / 16 * 100%);
  margin-top: rem(30);

  @include mq('md') {
    margin-top: rem(20);
  }
}