@use "global" as *;

.p-page-mv {
  padding: rem(198) $padding-sp;

  @include mq('md') {
    padding: rem(168) $padding-pc;
  }
}

.p-page-mv__title {
  font-size: rem(30);
  font-weight: 700;
  letter-spacing: rem(2);
  color: $color-white;
  text-align: center;

  @include mq('md') {
    font-size: rem(35);
  }
}