@use "global" as *;

.p-tabs {
  display: flex;
  flex-wrap: wrap;
  margin-left: rem(-20);
  margin-top: rem(-10);
}


.p-tabs__item {
  margin-left: rem(20);
  margin-top: rem(10);
}

.p-tabs__item a {
  display: inline-block;
  padding: rem(3) rem(10);
  letter-spacing: rem(2);
}

.p-tabs__item a.is-active {
  background: $color-white;
  color: $color-black;
}