@use "global" as *;

.p-sec-overview__body {
  position: relative;
  margin-top: rem(46);

  @include mq('md') {
    margin-top: rem(36);
    padding: rem(80) 0;
  }
}

.p-sec-overview__body::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  margin-left: calc(50% - 50vw);
  width: 100vw;
  height: 100%;
  background: $color-main;
  z-index: map-get($layer, bg);

  @include mq("md") {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    margin-right: calc(50% - 50vw);
    width: calc(896 / 1280 * 100vw);
    height: 100%;
    background: $color-main;
    z-index: map-get($layer, bg);
  }
}