@use "global" as *;

.p-works-card {
  position: relative;
  display: block;
}

.p-works-card__img {
  position: relative;
  width: 100%;
}

.p-works-card__img::after {
  content: "";
  display: block;
  width: 100%;
  padding-top: calc(249 / 375 * 100%);

  @include mq('md') {
    padding-top: calc(349 / 525 * 100%);
  }
}

.p-works-card__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
}

.p-works-card__title {
  font-size: rem(20);
  font-weight: 500;
  letter-spacing: rem(2);
  text-align: center;
  margin-top: rem(24);

  @include mq('md') {
    margin-top: rem(12);
  }
}

.p-works-card__label {
  position: absolute;
  top: 0;
  right: 0;
  display: inline-block;
  padding: rem(6) rem(20);
  background: $color-black;
  letter-spacing: rem(2);
}