@use "global" as *;

.p-modal__bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: $color-black;
  z-index: map-get($layer, modal);
  display: none;
}

.p-modal__list {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
  display: block;
  z-index: map-get($layer, modal) + 1;
  display: none;
}

.p-modal__item {
  width: 100%;
}

.p-modal__item + .p-modal__item {
  margin-top: rem(32);
}
