@use "global" as *;

.p-page-works .p-page-mv {
  background: transparent url(../img/works/mv_works_sp.jpg) no-repeat center center / cover;

  @include mq('md') {
    background: transparent url(../img/works/mv_works_pc.jpg) no-repeat center center / cover;
  }
}

.p-page-works__body {
  margin-top: rem(85);
  
  @include mq('md') {
    margin-top: rem(115);
  }
}

.p-page-works__content {
  margin-top: rem(17);
  display: none;

  @include mq('md') {
    margin-top: rem(40);
  }
}

.p-page-works__pagination {
  margin-top: rem(80);

  @include mq('md') {
    margin-top: rem(104);
  }
}