@use "global" as *;

.p-page-blog .p-page-mv {
  background: transparent url(../img/blog/mv_blog_sp.jpg) no-repeat center center / cover;

  @include mq('md') {
    background: transparent url(../img/blog/mv_blog_pc.jpg) no-repeat center center / cover;
  }
}

.p-page-blog__body {
  margin-top: rem(85);

  @include mq('md') {
    margin-top: rem(115);
  }
}

.p-page-blog__content {
  margin-top: rem(37);
  display: none;

  @include mq('md') {
    margin-top: rem(40);
  }
}

.p-page-blog__pagination {
  margin-top: rem(80);

  @include mq('md') {
    margin-top: rem(104);
  }
}

.p-page-blog__pagination .wp-pagenavi a:hover,
.p-page-blog__pagination .wp-pagenavi a:focus,
.p-page-blog__pagination .wp-pagenavi span.current {
  color: $color-black;
  background-color: $color-yellow-dark;
  border: rem(1) solid $color-yellow-dark;
}


