@use "global" as *;

.p-section-header {
  position: relative;
}

.p-section-header__title {
  font-size: rem(20);
  text-align: center;
  letter-spacing: rem(2);

  @include mq("md") {
    font-size: rem(30);
    letter-spacing: rem(0.5);
  }
}

.p-section-header__subtitle {
  bottom: rem(4);
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  font-size: 50px;
  font-family: $second-font-family;
  color: $color-yellow-dark;
  opacity: 0.2;
  z-index: map-get($layer, bg) - 1;

  @include mq("md") {
    font-size: rem(110);
    left: 0;
    transform: translateX(0);
    bottom: rem(-48);
    line-height: 1;
    letter-spacing: rem(2);
  }
}

.p-section-header--rev .p-section-header__subtitle {
  @include mq('md') {
    left: auto;
    right: 0;
  }
}

.p-section-header--contact .p-section-header__subtitle{
  z-index: map-get($layer, bg) + 1;

  @include mq("md") {
    bottom: rem(-2);
  }
}
