@use "global" as *;

//ギャラリー
.p-gallery {
  width: 100vw;
  margin: 0 calc(50% - 50vw);
  overflow: hidden;

  @include mq('md') {
    width: 100%;
    margin: 0;
  }
}

//メイン
.p-gallery__slider {
  width: 100%;
  height: auto;
  position: relative;
}

.p-gallery__slider::after {
  content: "";
  display: block;
  width: 100%;
  padding-top: calc(251 / 375 * 100%);
}

.p-gallery__slider .swiper-wrapper {
  position: absolute;
  top: 0;
  left: 0;
}

.p-gallery__slider .swiper-slide {
  width: 100%;
}

.p-gallery__slider img {
  display: block;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  object-fit: cover;
}

.p-gallery__slider .swiper-button-prev,
.p-gallery__slider .swiper-button-next {
  display: block;
  width: rem(50);
  height: rem(50);
  background: $color-black;
  border-radius: 50%;
}

.p-gallery__slider .swiper-button-prev::after,
.p-gallery__slider .swiper-button-next::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: rem(20);
  height: rem(20);
  border-left: rem(3.5) solid $color-yellow-light;
  border-top: rem(3.5) solid $color-yellow-light;
}

.p-gallery__slider .swiper-button-prev::after {
  transform: translate(-25%, -50%) rotate(-45deg);
}

.p-gallery__slider .swiper-button-next::after {
  transform: translate(-75%, -50%) rotate(135deg);
}



//サムネイル
.p-gallery__thumbs {
  width: calc(163 / 375 * 100%);
  margin-top: rem(30);
  position: relative;

  @include mq('md') {
    width: calc((100% - 8px * 7) / 8);
    margin-top: rem(40);
  }
}

.p-gallery__thumbs::after {
  content: "";
  display: block;
  width: 100%;
  padding-top: 100%;
}

.p-gallery__thumbs .swiper-wrapper {
  position: absolute;
  top: 0;
  left: calc(50% + 24px);

  @include mq('md') {
    left: 0;
  }
}

.p-gallery__thumbs .swiper-slide {
  width: 100%;
  height: 100%;
  text-align: center;
  overflow: hidden;
}

.p-gallery__thumbs img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
