@use "global" as *;

.p-single-pagination {
  display: flex;
  align-items: center;
  justify-content: center;
}

.p-single-pagination__prev a,
.p-single-pagination__archive a,
.p-single-pagination__next a {
  display: inline-block;
  width: rem(71);
  text-align: center;
  border: rem(1) solid $color-white;
  transition: .3s ease;
} 

.p-single-pagination__prev a:hover,
.p-single-pagination__prev a:focus,
.p-single-pagination__archive a:hover,
.p-single-pagination__archive a:focus,
.p-single-pagination__next a:hover,
.p-single-pagination__next a:focus {
  color: $color-main;
  background: $color-white;
}

.p-single-pagination__prev span,
.p-single-pagination__next span {
  display: inline-block;
  width: rem(71);
}

.p-single-pagination__archive {
  margin-left: rem(48);
}

.p-single-pagination__next {
  margin-left: rem(48);
}