@use "global" as *;

.p-sec-contact {
  padding: rem(69) 0 rem(64);
  background: $color-main;

  @include mq("md") {
    padding: rem(144) 0 rem(148);
  }
}

.p-sec-contact__body {
  margin-top: rem(12);
}

.p-sec-contact__text {
  letter-spacing: rem(2);
  margin-top: rem(20);
}

.p-sec-contact__footer {
  margin-top: 47px;
  text-align: center;

  @include mq("md") {
    margin-top: rem(60);
    font-size: rem(20);
  }
}
