@use "global" as *;

.p-works-cards {
  @include mq('md') {
    display: flex;
    flex-wrap: wrap;
  }
}

.p-works-cards__item + .p-works-cards__item {
  margin-top: rem(48);

  @include mq('md') {
    margin-top: 0;
  }
}

.p-works-cards__item {
  width: 100%;
  
  @include mq('md') {
    width: calc(50% - rem(48) / 2);
  }
}

.p-works-cards__item:nth-child(2n) {
  @include mq('md') {
    margin-left: rem(48);
  }
}


.p-works-cards__item:nth-child(n + 3) {
  @include mq('md') {
    margin-top: rem(110);
  }
} 

