@use "global" as *;

.p-single-works__breadcrumbs {
  margin-top: rem(66);
}

.p-single-works__body {
  margin-top: rem(50);

  @include mq('md') {
    margin-top: rem(90);
  }
}

.p-single-works__inner.l-inner {
  max-width: calc(rem(800) + $padding-pc * 2);
}

.p-single-works__title {
  font-size: rem(20);
  letter-spacing: rem(.4);

  @include mq('md') {
    font-size: rem(23);
    font-weight: 500;
  }
}

.p-single-works__info {
  display: flex;
  align-items: center;
  margin-top: rem(16);

  @include mq('md') {
    margin-top: rem(32);
  }
}

.p-single-works__date {
  display: inline-block;
  letter-spacing: rem(.4);
}

.p-single-works__genre {
  display: inline-block;
  letter-spacing: rem(2);
  padding: rem(6) rem(20);
  border: rem(1) solid $color-white;
  margin-left: rem(30);
}

.p-single-works__gallery {
  margin-top: rem(16);

  @include mq('md') {
    margin-top: rem(32);
  }
}

.p-single-works__boxes {
  margin-top: rem(32);

  @include mq('md') {
    margin-top: rem(80);
  }
}

.p-single-works__box + .p-single-works__box {
  margin-top: rem(32);

  @include mq('md') {
    margin-top: rem(48);
  }
}

.p-single-works__pagination {
  margin-top: rem(80);

  @include mq('md') {
    margin-top: rem(104);
  }
}

.p-single-works__related {
  margin-top: rem(120);

  @include mq('md') {
    margin-top: rem(104);
  }
}